







































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import { IsPC } from '../../../utils/index'
import { realFormatSecond } from '../../../utils/date'
@Component({
  directives: {
    zkcli: {
      // 指令的声明周期
      bind(el: any, bindings, vnode: any) {
        // context
        // 把事件绑定给 document 上 看一下是否点击的有效
        let down:any, onmove:any, up:any
        if (IsPC()) {
          down = 'mousedown'
          onmove = 'mousemove'
          up = 'mouseup'
        } else {
          down = 'touchstart'
          onmove = 'touchmove'
          up = 'touchend'
        }
        const handler = (e: any) => {
          if (el.contains(e.target)) {
            // console.log(e, '------')
            // vnode.context.width = e.offsetX
            // const e = ev || event
            // console.log(e, '------')
            let pointX
            pointX = e.offsetX || e.touches[0].clientX - el.getBoundingClientRect().left // 手指按下时的坐标x,距浏览器
            vnode.context.width = pointX
            vnode.context.aplayer.currentTime = vnode.context.maxTime * (vnode.context.width / vnode.context.contentwidth)
            const move = (ev:any) => {
              const e = ev || event
              pointX = e.clientX - el.getBoundingClientRect().left || e.touches[0].clientX - el.getBoundingClientRect().left
              // console.log(e.offsetX, '-----')
              if (pointX <= 0) { //  解决边境问题
                vnode.context.width = 0
                return false
              } else if (pointX >= vnode.context.contentwidth) {
                vnode.context.width = vnode.context.contentwidth
                return false
              }
              vnode.context.width = pointX
              vnode.context.aplayer.currentTime = vnode.context.maxTime * (vnode.context.width / vnode.context.contentwidth)
            }
            document.addEventListener(onmove, move, false)
            document.addEventListener(up, (ev:any) => {
              document.removeEventListener(onmove, move, false)
            })
            // console.log('包含')
          } else {
            // console.log('不包含')
          }
          e.stopPropagation()
          return false
        }
        el.handler = handler
        // document.addEventListener('click', handler)

        document.addEventListener(down, handler, false)
      },
      unbind(el: any) {
        document.removeEventListener('touchstart', el.handler)
        document.removeEventListener('mousedown', el.handler)
      }
    }
  },
  filters: {
    Time: (val:any) => {
      return realFormatSecond(val)
    }
  }
})
export default class extends Vue {
  @Prop({
    type: Object,
    default: () => ({
      url: 'http://file.duzhai.net/Files/Audio/2017-08/af01ab05-d112-4ad7-9cff-2cfecced5bc6.mp3'
    })
  })
  apyobj!:object

  get widthBtn() { // 返回按钮的width /2
    return -this.widthbtn
  }

  @Watch('isplay')
  onPlay(val: any, oldVal: any) {
    val ? this.play() : this.pause()
  }

  public isplay = false // 播放和暂停的 icon
  public width = 0 // 进度条的宽度
  public contentwidth = 0 // 总的长度
  public widthbtn = 0 // 按钮的width
  public lodingWidth = 0
  public aplayer:any = '' // 播放器的dom
  public currentTime:number= 0// 音频当前播放时长
  public maxTime:number = 0// 音频最大播放时长
  public plays() {
    this.isplay = !this.isplay
  }

  async created() {
    await this.$nextTick()
    const whites:any = this.$refs.aplayerSlider // 总长度
    this.contentwidth = whites.offsetWidth
    const apyDrag:any = this.$refs.apyDrag
    const Dragbtn = apyDrag.querySelector('.aplayer-btn')
    this.widthbtn = Dragbtn.offsetWidth / 2 // 按钮的位置
    this.aplayer = this.$refs.aplayer // 播放器的dom
    this.aplayer.load() // 预加载
    // console.log(this.aplayer.buffered, ' this.widthbtn----')
    // this.aplayer.addEventListener('progress', (e) => {
    //   const percent = this.aplayer.buffered.length ? (this.aplayer.buffered.end(this.aplayer.buffered.length - 1) / this.aplayer.duration) : 0
    //   console.log(percent, '--aaaaaaa----')
    // })
  }

  // 播放音频
  public play() {
    this.aplayer.play()
  }
  // 当音频暂停
  public onPause() {
    this.isplay = false
  }
  public onPlays(e:any) {
    const target = e.target
    const audios:any = document.getElementsByTagName('audio');
    // 如果设置了排他性，当前音频播放是，其他音频都要暂停
    [...audios].forEach((item) => {
      if (!item.contains(target)) {
        item.pause()
      }
    })
    this.isplay = true
  }
  // 暂停音频
  public pause() {
    this.aplayer.pause()
  }

  // 当音频当前时间改变后，进度条也要改变
  public onTimeupdate(e:any) { // 监听时间的改变
    // console.log('timeupdate')
    // console.log(e, '------', e.target.currentTime)
    this.currentTime = e.target.currentTime
    this.width = this.contentwidth * (this.currentTime / this.maxTime)
    if (this.currentTime >= this.maxTime) {
      this.isplay = false
    }
  }
  // 语音元数据主要是语音的长度之类的数据
  public onLoadedmetadata(e:any) {
    this.maxTime = parseInt(e.target.duration)
  }

  public progress(e:any) { // 获取文件是否加载完毕
    const percent:number = this.aplayer.buffered.length ? (this.aplayer.buffered.end(this.aplayer.buffered.length - 1) / this.aplayer.duration) : 0
    this.lodingWidth = percent * 100
    // console.log(percent, '--aaaaaaa----')
  }
}

