











import { Component, Vue, Prop, Emit } from 'vue-property-decorator'

@Component({
  name: 'zkRotor'
})
export default class extends Vue {
  public prizelist:any[] = [] // 奖品名称 ;
  public startdeg:number = 0 // 最少多少圈
  public Loading:boolean = false
  public prizelistDeg:any[]=[]

 public rotate = 0;

   @Prop({
     type: Object,
     default: () => (
       {
         prizelist: [], // 奖品名称
         startdeg: 6,
         rotorimg: () => (require('@/assets/images/dzp.jpg')),
         btnimg: () => (require('@/assets/images/draw_btn.png'))
       }
     )
   })
  rotorObj!:any

  @Emit('rotorquery')
   rotorquery(item:any) {
     return item
   }
  public moveRotate() {
    if (this.Loading) return // 在动画完成前只能点击一次
    this.Loading = true
    const prizelist = this.prizelist // 奖品内容
    const deg = this.mathRan(prizelist.length) // 随机数
    // console.log(deg, 'deg-------', prizelist[deg - 1])
    const remaindeg = 360 - (this.rotate % 360) // 360 的余数
    // console.log(remaindeg, '--remaindegremaindegremaindeg-----')
    //  console.log(...this.prizelistDeg)
    // console.log(this.prizelistDeg[deg - 1]) // 奖品
    //  this.rotate += this.prizelistDeg[deg - 1]
    this.rotate += this.prizelistDeg[deg - 1] + remaindeg + this.startdeg
    const rotor:any = this.$refs.rotor
    rotor.addEventListener('transitionend', () => {
      // 获奖后的所有数据抛出
      this.$dialog.alert({
        message: this.prizelist[deg - 1].name
      }).then(() => this.rotorquery(this.prizelist[deg - 1]))
      this.Loading = false // 动画完成以后公布答案
    }, false)
  }

  get prizelists() {
    return this.prizelist.reduce((prev, next, index, attr) => {
      const equall = 360 / attr.length
      if (index !== attr.length) {
        prev.push(equall / 2 + equall * index)
      }
      return prev
    }, [])
  }
  created() {
    const { startdeg, prizelist }:any = this.rotorObj
    this.startdeg = 360 * startdeg
    this.prizelist = prizelist
    this.prizelistDeg = this.prizelists
  }
  public mathRan(s:number) { // 设置随机整数
    return Math.floor((Math.random() * s) + 1)
  }
}

