export const formatDate = (value:any, fmt:any) => { // 格式化年月日 时分秒
  if (value !== null) {
    const d = new Date(value)
    const o:any = {
      'M+': d.getMonth() + 1, // 月份
      'd+': d.getDate(), // 日
      'H+': d.getHours(), // 小时
      'm+': d.getMinutes(), // 分
      's+': d.getSeconds(), // 秒
      'q+': Math.floor((d.getMonth() + 3) / 3), // 季度
      'S': d.getMilliseconds() // 毫秒
    }
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (d.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (const k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
      }
    }
    return fmt
  }
}

export const datespace = (value:any, value1:any) => { // 取日期直接相差多少天
  if (value && value1) {
    const d = new Date(value)
    const d1 = new Date(value1)
    const [dtime, d1time] = [d.getTime(), d1.getTime()]
    const endtime:any = (d1time - dtime) / 1000 / 3600 / 24
    return parseInt(endtime)
  }
}

export const getYearMontDay = (date: Date) => { // 返回年月日
  const year = date.getFullYear()
  const month = date.getMonth()
  const day = date.getDate()

  return { year, month, day }
}

export const getDate = (year: any, month: any, day: any) => { // 返回时间
  return new Date(year, month, day)
}

// 将整数转换成 时：分：秒的格式
export const realFormatSecond = (second:any) => {
  const secondType = typeof second

  if (secondType === 'number' || secondType === 'string') {
    second = parseInt(second)

    const hours = Math.floor(second / 3600)
    second = second - hours * 3600
    const mimute = Math.floor(second / 60)
    second = second - mimute * 60

    return hours + ':' + ('0' + mimute).slice(-2) + ':' + ('0' + second).slice(-2)
  } else {
    return '0:00:00'
  }
}

