

















































import { Component, Vue, Watch } from 'vue-property-decorator'
@Component
export default class extends Vue {
  private active:string = 'home'

  created() {
    if (this.$route.path === '/views/orders') {
      this.active = 'orders'
    }
  }

  public goinfo(val:any) {
    this.$router.push(`/views/${val}`)
  }
}

