















































import { Component, Vue, Prop, Emit } from 'vue-property-decorator'
@Component
export default class extends Vue {
  @Prop({
    type: Object,
    default: () => ({
      leftimg: [
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false }
      ],
      rightimg: [
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false },
        { address: require('@/assets/images/tangqing.gif'), clicks: false, isshow: false }
      ]
    })
  })
  queryList!:any

  public canvas:any ='' // 画布
  public ctx:any = '' // 画笔
  public lint:any = { // 左边和右边的数据
    lx: 0,
    ly: 0,
    rx: 0,
    ry: 0
  }
  public lintitem:any = { // 左边和右边的数据
    leftArr: [],
    rightArr: []
  }
 public lintList:any[] = []; // 保存所有的线 和数据
 public w=0 // 画布的宽高
 public h=0 // 画布的宽高
 public left = true // 初始是从左向右
 public setIntervals:any = '' // 动画定时
 @Emit('addToCount')
 addToCount(lintList:any) {
   return lintList
 }
 get islint() {
   const lint = this.lint
   return Object.values(lint).every((item) => item !== 0) // 判断左右都不为 零的时候在连线
 }
 async created() {
   await this.$nextTick()
   this.canvas = this.$refs.canvas
   const content = this.$refs.content
   const canvas:any = this.$refs.canvas
   const { offsetWidth: width, offsetHeight: height }:any = content
   this.w = width
   this.h = height
   canvas.width = width
   canvas.height = height
   this.ctx = canvas.getContext('2d')
   this.ctx.lineWidth = 0.5
   this.ctx.arc(0, 0, 100, 0, Math.PI * 2, true)
   this.ctx.fillStyle = `red`
   this.ctx.stroke()
   this.ctx.fill()
 }

 public canvasLeft(leftdom:any, item:any) {
   this.left = true
   let { x, y } = document.querySelector(leftdom).getBoundingClientRect()
   y += document.documentElement.scrollTop
   x += document.documentElement.scrollLeft
   this.lint = { ...this.lint, lx: x, ly: y } // 覆盖重复的数据
   const lint = this.lint
   this.lintitem.leftArr = item
   if (this.islint) {
     this.isattr(lint) // 删除重复的数据
     this.lintList.push({ ...lint, ...this.lintitem })
     this.drawLines(this.lintList) // 连接多条线
     this.addToCount(this.lintList)
   }
 }

 public canvasRight(rightdom:any, item:any) {
   this.left = false
   let { x, y }:any = document.querySelector(rightdom).getBoundingClientRect()
   y += document.documentElement.scrollTop
   x += document.documentElement.scrollLeft
   this.lint = { ...this.lint, rx: x, ry: y } // 覆盖重复的数据
   const lint = this.lint
   this.lintitem.rightArr = item
   if (this.islint) {
     this.isattr(lint) // 删除重复的数据
     this.lintList.push({ ...lint, ...this.lintitem })
     this.drawLines(this.lintList)// 连接多条线
     this.addToCount(this.lintList)
   }
 }

 public drawLine(lint:any) { // 连线
   const ctx = this.ctx
   const color = ctx.createLinearGradient(lint.lx, lint.ly, lint.rx, lint.ry)
   color.addColorStop(0, '#0def58')
   color.addColorStop(1, '#003cff')
   ctx.save()
   ctx.strokeStyle = color
   ctx.beginPath()
   ctx.moveTo(lint.lx, lint.ly)
   ctx.lineTo(lint.rx, lint.ry)
   ctx.closePath() // 结束路径
   ctx.stroke() // 线条方法
 }
 public drawLines(lintList:any) { // 连接多条线
   this.ctx.clearRect(0, 0, this.w, this.h)
   lintList.forEach((element:any, index:number, attr:any) => {
     if (index === attr.length - 1) {
       this.ligature(element.lx, element.ly, element.rx, element.ry) // 动画效果
       return false
     }
     this.drawLine(element) // 连线
   })
   //  this.drawLine(lint)
   for (const item of Object.keys(this.lint)) {
     this.lint[item] = 0
   } // 连线完成后清空数据
 }

 // 判断是否有重复的值， 有的画就返回index
 public isattr(lr:any):void {
   this.lintList.forEach((item, index, att) => {
     if (item.ly === lr.ly) {
       att.splice(index, 1)
     }
   })
   this.lintList.forEach((item, index, att) => {
     if (item.ry === lr.ry) {
       att.splice(index, 1)
     }
   })
 }

 // 最后一条线的动画

 ligature(lx:any, ly:any, rx:any, ry:any) {
   // 斜率
   const k = (rx - lx) / (ry - ly)
   let rlx = lx
   let rly = ly
   let llx = rx
   let lly = ry
   const color = this.ctx.createLinearGradient(lx, ly, rx, ry)
   color.addColorStop(0, '#0def58')
   color.addColorStop(1, '#e63022')
   this.ctx.beginPath()
   this.ctx.strokeStyle = color
   this.ctx.arc(0, 0, 100, 0, Math.PI * 2, true)
   this.ctx.stroke()
   this.ctx.closePath()
   this.ctx.save()
   this.ctx.beginPath()
   // 右左连接
   let setIntervals:any
   let fn:any
   //  const requestAnimationFrame :any = window.requestAnimationFrame // 简单兼容
   //  if (!window.requestAnimationFrame) {
   //    const requestAnimationFrame = function(fn) {
   //      setTimeout(fn, 17)
   //    }
   //  }
   if (this.left) {
     this.ctx.moveTo(rx, ry)
     this.ctx.stroke()
     setIntervals = requestAnimationFrame(fn = () => {
       llx += -10
       lly = ly - (lx - llx) / k
       this.ctx.lineTo(llx, lly)
       this.ctx.stroke()
       setIntervals = window.requestAnimationFrame(fn)
       if (llx <= lx) {
         window.cancelAnimationFrame(setIntervals)
       }
     })
   } else {
     // 左右连接
     this.ctx.moveTo(lx, ly)
     setIntervals = requestAnimationFrame(fn = () => {
       rlx += 10
       rly = ry - (rx - rlx) / k
       this.ctx.lineTo(rlx, rly)
       this.ctx.stroke()
       setIntervals = window.requestAnimationFrame(fn)
       if (rlx >= rx) {
         window.cancelAnimationFrame(setIntervals)
       }
     })
   }
 }
}

