











import { Component, Vue, Prop, Emit } from 'vue-property-decorator'

@Component
export default class extends Vue {
  @Prop({
    type: Object,
    default: () => ({
      Prizeimg: require('@/assets/images/youku_05.png'),
      canvimgs: require('@/assets/images/xygj.jpg')
    })
  })
  queryImg!:any

  public isshow:boolean =false // 奖品是否显示
  public iscanvas:boolean =false // 动画\
  public ctx:any ='' // 画笔
  public w = 0 // 画布的宽高
  public h = 0 // 画布的宽高
  public z = 0 /* 画过后的百分比 */

  @Emit('scrquery')
  scrquery(item:any) {
    return item
  }
  async created() {
    await this.$nextTick()
    const canvas:any = this.$refs.canvas
    const content = this.$refs.content
    const { offsetWidth: width, offsetHeight: height }:any = content
    canvas.width = width
    canvas.height = height
    this.w = width
    this.h = height
    this.ctx = canvas.getContext('2d')
    this.startCanvas()
    this.move()
    // document.querySelector('.ddd').addEventListener('touchstart', (ev) => {
    //   console.log('aaaa')
    // })
  }

  public startCanvas() { // 初始化 canvas
    const ctx:any = this.ctx
    ctx.strokeStyle = '#f36651'
    const image:any = new Image()
    image.src = this.queryImg.canvimgs
    image.onload = () => { // 加载图片
      ctx.drawImage(image, 0, 0, this.w, 500)
      this.isshow = true
    }
  }

  public IsPC() { // 判断移动端 or PC
    var userAgentInfo = navigator.userAgent
    const Agents = ['Android', 'iPhone',
      'SymbianOS', 'Windows Phone',
      'iPad', 'iPod'
    ]
    let flag = true
    for (var v = 0; v < Agents.length; v++) {
      if (userAgentInfo.indexOf(Agents[v]) > 0) {
        flag = false
        break
      }
    }
    return flag
  }
  public move() { // 刮奖
    let down:any, onmove:any, up:any
    if (this.IsPC()) {
      down = 'mousedown'
      onmove = 'mousemove'
      up = 'mouseup'
    } else {
      down = 'touchstart'
      onmove = 'touchmove'
      up = 'touchend'
    }
    const ctx = this.ctx
    const canDom:any = document.querySelector('#canvas')
    console.log(down, onmove, up, 'ssssss')
    let fn:any
    canDom.addEventListener(down, fn = (ev:any) => {
      const e = ev || event

      ctx.moveTo(e.offsetX || e.touches[0].clientX - canDom.offsetLeft, e.offsetY || e.touches[0].clientY - canDom.offsetTop)
      ctx.lineTo(e.offsetX || e.touches[0].clientX - canDom.offsetLeft, e.offsetY || e.touches[0].clientY - canDom.offsetTop)
      ctx.lineWidth = 50
      // 设置绘制状态
      // eslint-disable-next-line no-irregular-whitespace
      ctx.lineCap = 'round'　　 // 设置线条两端为圆弧
      // eslint-disable-next-line no-irregular-whitespace
      ctx.lineJoin = 'round'　　 // 设置线条转折为圆弧
      /* 设置新绘制清除新绘制内容和原内容的重叠区域*/
      ctx.globalCompositeOperation = 'destination-out'
      const move = (ev:any) => {
        const e = ev || event
        ctx.lineTo(e.offsetX || e.touches[0].clientX - canDom.offsetLeft, e.offsetY || e.touches[0].clientY - canDom.offsetTop)
        ctx.stroke()
        this.handleFilledPercentage(this.getFilledPercentage(), () => {
          canDom.removeEventListener(down, fn, false)
          canDom.removeEventListener(onmove, move, false)
        }
        )
      }
      canDom.addEventListener(onmove, move, false)
      canDom.addEventListener(up, (ev:any) => {
        ctx.stroke()
        canDom.removeEventListener(onmove, move, false)
      })
      return false
    })
  }

  // 计算已经刮过的区域占整个区域的百分比
  public getFilledPercentage() {
    const ctx:any = this.ctx
    if (!ctx) return 0.00
    const imgData = ctx.getImageData(0, 0, this.w, this.h)
    // imgData.data是个数组，存储着指定区域每个像素点的信息，数组中4个元素表示一个像素点的rgba值
    const pixels = imgData.data
    const transPixels = []
    for (let i = 0; i < pixels.length; i += 4) {
      // 严格上来说，判断像素点是否透明需要判断该像素点的a值是否等于0，
      // 为了提高计算效率，这儿设置当a值小于128，也就是半透明状态时就可以了
      if (pixels[i + 3] < 128) {
        transPixels.push(pixels[i + 3])
      }
    }
    return (transPixels.length / (pixels.length / 4) * 100).toFixed(2)
  }

  public handleFilledPercentage(percentage:any, fn:any) { // 设置阈值，去除灰色涂层
    const percentages:any = percentage || 0
    this.z = parseInt(percentages)
    if (parseInt(percentages) >= 40) {
      this.iscanvas = true
      fn()
      this.scrquery('刮奖完毕')
    }
  }
}

